import React from 'react';
import './blog.css';

class Blog extends React.Component {
    render(){
        return(
            <section className="blog-post">
                <div className="blog">
                    <div className="date">
                        7 April 2020
                    </div>
                    <h2 className="blog-title">The quality of life of senior citizens of Kyrgyz Republic.</h2>
                            <h2 className="blog-title"> Indicators and standards.</h2>
                    <div className="chapters">
                        <p>Introduction.</p>
                        <ul>
                            <li>Relevance of research.</li>
                            <li>Research tasks.</li>
                            <li>Object, subject and purpose of research.</li>
                            <li>Methodology of solving problem.</li>
                        </ul>
                    </div>
                    <div className="chapters">
                        <p>General concept of aging and elderly people.</p>
                        <ul>
                            <li>The main age categories and their relationship with life situation. </li>
                            <li>The main categories of people: elderly, old, long-livers.</li>
                            <li>General characteristics of the concept of a difficult life situation.</li>
                        </ul>
                    </div> 
                    <div className="chapters">
                        <p>General concepts of quality of life and its features in the Kyrgyz Republic.</p>
                        <ul>
                            <li>Indicators and standards of life in science and practical life.</li>
                            <li>The role of social workers in supporting and helping older people in difficult life situation.</li>
                        </ul>
                    </div>    
                    <div className="chapters">
                        <p>Conclusion.</p>
                        <ul><li>
                             The results of the research analysis of the problems,
                             conclusions and recommendations,
                              patterns of aging, presentation of premature aging.
                            </li>
                        </ul>
                    </div>       
                    <p className="paragraph"><span>The relevance of research.</span> The problem of old
                    age is relevant not only for the elderly, but also for the whole
                    society. Age is an important characteristic for every person.
                    Worthy old age in many respects depends on a person’s efforts
                    for self-development throughout his life and his cultural level.
                    International Day of Older Persons is celebrated on October 1st.
                    The purpose of the Day of Older Persons is to draw attention to the
                    problems and needs of the elderly, to maintain their sense of need
                    for society, to increase public awareness of the demographic aging
                    of society. In the Kyrgyz Republic over the past 5 years, the number
                    above working age has increased by 18 percent. According to the definition
                    of the National Statistical Committee and the Ministry of Social Development
                    and Labor, men 63 and older than 63 years old, and women 58 and older than
                    58 years old is considered an elderly person. The gap between the female
                    and male sex is explained by the fact that the probability of surviving
                    to retirement age in women is more than men. And also take into account
                    the retirement age of women comes five years earlier (for men - at 63 years
                    old, for women - at 58 years old). Modern 60-year-old women are more likely
                    to live on average about 20 years, men - about 15 years. In 2016, life
                    expectancy for men was 67.0 years, for women - 75.1 years. For the existence
                    of such social phenomena, some arguments are due to a related factor directly
                    affecting human life. This may be alcoholism, smoking, patterns of aging
                    or accidents. Gerontologists have not come to a single conclusion that heredity
                    or the environment is more important? Heredity plays a big role, but the external
                    environment is more important than the genetic component of the body.</p> <br />
                        
                    <p className="paragraph"><span>The purpose of the research.</span> The purpose
                    of this study is to study the state of health, lifestyle, well-being and
                    well-being of people over 58 years of age. Determine the needs, as well as
                    the measurement of social services that is provided by the state. The
                    meaning is how much a person lives in a prosperous society, and how 
                    comfortable he feels. And also to get more specific information for
                    processing and analyzing the current situation.To improve the quality
                    of life, I need to evaluate the current situation using certain parameters.
                    In the future, it is necessary to process information and derive recommendations
                    to improve the quality of social services.</p><hr />
                        
                    <p className="paragraph"><span>The object</span> of the scientific research is
                    elderly people, people in stationary institutions, single elderly people,
                    people with disabilities, people in difficult situations. Such institutions
                    include nursing homes for the elderly and disabled,a home for single 
                    elderly people, gerontological centers.</p>
                     
                    <p className="paragraph"><span>Subject research and methodology.</span> The subject
                    of my research is a person in society,human rights, the relationship between
                    clients and a social worker, principles and methods of implementing social
                    services, laws and regulations guaranteeing the provision of social
                    services for people with disabilities, indicators and indicators
                    of quality of life. For the study, surveys are used to assess needs, and expert
                    interviews, desk research.</p>
                    <p className="paragraph"><span>Research Task.</span> What technologies
                    are used to perform social services.Identify traditional and innovative methods.
                    Compare international standards of social services with state standards
                    of quality of social services. Hear the opinions of consumers of social 
                    services. Are they satisfied? Determine the level of quality.</p>
                </div>
            </section>
        )
    }
}

export default Blog;